import React, { Component} from 'react'
import DocumentTitle from 'react-document-title'
import { Button, Table, Tag, Space, Pagination, Menu, Layout, Dropdown, Input, Modal, Form, message,Spin } from 'antd';
import { DownOutlined, PlusCircleOutlined } from '@ant-design/icons';
import request from '../../utils/request'
import ChangeTags from '../../components/ChangeTags'
import store from '../../store'
import './index.less'

const { Header, Footer, Content } = Layout;
const { Search, TextArea } = Input;


export default class index extends Component {
    state = {
        data:[],
        tableHeight:0,
        loadingState:false,
        isCreateProjectVisible:false,
        createLoading:false,
        form:{
          current:1,
          pageSize:10,
          pageSizeOptions:[5,10,15,20],
          showSizeChanger:true,
          total:50
        },
        createForm:{
          name:"",
          grade_range:[],
          subjects:[],
          description:"",
          requirement:"",
          summary:"",
          target:"",
        }
    }

    createFormRef = React.createRef()

    columns = [
        {
          title: '',
          key: 'name',
          align: 'center',
          width: 210,
          render: (text,record) => (
            // <Space size="middle">
            //   <Button type="link" onClick={this.seekProjectManagement.bind(this,record)}>{
            //     record.basic_info.name
            //   }</Button>
            // </Space>
            <span>{record.basic_info.name}</span>
          )
        },
        {
          title: '',
          key: 'subject',
          dataIndex: 'subject',
          align: 'center',
          width: 210,
          render: (text,record) => (
            <>
              {
                record.basic_info.subjects.map((item,index)=>(
                  <Tag key={index} color="green">
                    {
                      item
                    }
                  </Tag>
                ))
              }
            </>
          )
        },
        {
            title: '',
            key: 'period',
            align: 'center',
            render: (text,record) => (
              <>
                {
                  record.basic_info.grade_range.map((item,index)=>(
                    <Tag key={index} color="green">
                      {
                        item
                      }
                    </Tag>
                  ))
                }
              </>
            ),
        },
        {
            title: '',
            key: 'paper',
            align: 'center',
            width: 142,
            render: record => (
              <span>0</span>
            )
        },
        {
            title: '',
            key: 'questions',
            align: 'center',
            width: 121,
            render: record => (
              <span>0</span>
            )
        },
        {
            title: '',
            dataIndex: 'CreateAt',
            key: 'create-time',
            align: 'center',
            width: 342,
            render: (text,record) => (
              <span>{this.dateFilter(record.CreateAt)}</span>
            )
        },
        {
          title:"",
          key:'title',
          align: 'center',
          render: (text, record) => (
            <Space size="middle">
              <Button type="link" onClick={this.seekProjectManagement.bind(this,record)}></Button>
              <Button type="link" danger></Button>
            </Space>
          )
        }
    ]
    pendingTaskMenu = ()=>{
      return (
          <Menu>
              <Menu.Item key="1">1</Menu.Item>
              <Menu.Item key="2">2</Menu.Item>
              <Menu.Item disabled key="3">3</Menu.Item>
              <Menu.Item key="4">4</Menu.Item>
          </Menu>
      )
    }
    componentDidMount = ()=>{
      this.getProjectList()
    }
    seekProjectManagement = (state)=>{
      this.props.history.push(`/home/project-management/${state.project_id}/${state.role}`)
    }

    dateFilter(time){
      let date = new Date(time)
      return `${date.getFullYear()}-${date.getMonth().toString().padStart(2,'0')}-${date.getDate().toString().padStart(2,'0')}`
    }

    getProjectList = ()=>{
      this.setState({
        loadingState:true
      })
      request({ method:'GET', url:`http://49.232.73.36:8081/review/proj/user/${store.getState().userInfo.Id}`}).then(res=>{
        let id_list = res.data.map(item=>item.project_id)
        let role_lits = res.data.map(item=>item.role)
        let project_id_list = res.data.map(item=>item.project_id)
        request({
          url:"http://49.232.73.36:8081/review/query/proj",
          method:"POST",
          data:{
            id_list
          }
        }).then(res=>{
          let data = Object.values(res.data)
          data = data.map((item,index)=>{
            item.role = role_lits[index]
            item.project_id = project_id_list[index]
            return item
          })
          this.setState({
            data,
            loadingState:false
          })
        }).catch(err=>{
          this.setState({
            loadingState:false
          })
          message.error(err.message||"")
        })
      }).catch(err=>{
        this.setState({
          loadingState:false
        })
        message.error(err.message||"")
      })
    }



    render() {
        return (
            <DocumentTitle title="-">
                <Layout className="pending-tasks-page" data-component="pending-tasks-page">
                  <Header>
                    <span className="title">/</span>
                    <div className="right-box">
                      <Search placeholder="input search text" style={{width:'2.64rem',height:'.32rem'}}/>
                      <Dropdown overlay={this.pendingTaskMenu()}>
                          <span><DownOutlined /></span>
                      </Dropdown>
                      <Button type="primary" icon={<PlusCircleOutlined />} onClick={()=>{
                        this.setState({
                          isCreateProjectVisible:true
                        })
                      }}></Button>
                    </div>
                  </Header>
                  <Content>
                    <Table 
                      loading={this.state.loadingState}
                      columns={this.columns} 
                      dataSource={this.state.data} 
                      rowSelection={{}} 
                      size="small" 
                      rowKey="Id"
                      pagination={false}
                      scroll={{ y: 'calc(100vh - 2.2rem)'}}
                    />
                  </Content>
                  <Footer>
                    <Pagination
                      size="small"
                      total={85}
                      showSizeChanger
                      showQuickJumper
                    />
                    <Modal 
                      title="" 
                      visible={this.state.isCreateProjectVisible} 
                      cancelText=""
                      okText=""
                      closable={!this.state.createLoading}
                      confirmLoading={this.state.createLoading}
                      maskClosable={!this.state.createLoading}
                      keyboard={!this.state.createLoading}
                      onOk={()=>{
                        const form = this.createFormRef.current
                        form.validateFields().then(data=>{
                          this.setState({
                            createLoading:true
                          })
                          request({ method:'POST', url:"http://49.232.73.36:8081/review/proj/template",data}).then(res=>{
                            message.success("");
                            this.setState({
                              createLoading:false,
                              isCreateProjectVisible:false
                            })
                            this.createFormRef.current.resetFields()
                            this.getProjectList()
                          }).catch(err=>{
                            this.setState({
                              createLoading:false
                            })
                            message.error(err.message||"")
                          })
                        }).catch(err=>{
                          message.warning("")
                        })
                      }} 
                      onCancel={()=>{
                        if(this.state.createLoading){
                          message.error("")
                        }else{
                          this.createFormRef.current.resetFields()
                          this.setState({
                            isCreateProjectVisible:false
                          })
                        }
                      }}
                    >
                      <Spin spinning={this.state.createLoading} tip="">
                        <Form labelCol={{span: 4 }}  wrapperCol={{span: 20}} ref={this.createFormRef} name="createForm"  initialValues={this.state.createForm}>
                          <Form.Item name="name" label="" rules={[{ required: true,message: '' }]}>
                            <Input placeholder="" />
                          </Form.Item>
                          <Form.Item name="grade_range" label="" rules={[{ required: true,message:"" }]}>
                            <ChangeTags onChange={grade_range=>{
                              let createForm = Object.assign(this.state.createForm,{
                                grade_range
                              })
                              this.setState({
                                createForm
                              })
                            }}></ChangeTags>
                          </Form.Item>
                          <Form.Item name="subjects" label="" rules={[{ required: true,message:"" }]}>
                            <ChangeTags onChange={subjects=>{
                              let createForm = Object.assign(this.state.createForm,{
                                subjects
                              })
                              this.setState({
                                createForm
                              })
                            }}></ChangeTags>
                          </Form.Item>
                          <Form.Item name="target" label="" rules={[{ required: true,message:"" }]}>
                            <TextArea
                              placeholder=""
                              autoSize={{ minRows: 3, maxRows: 5 }}
                            />
                          </Form.Item>
                          <Form.Item name="summary" label="" rules={[{ required: true,message:"" }]}>
                            <TextArea
                              placeholder=""
                              autoSize={{ minRows: 3, maxRows: 5 }}
                            />
                          </Form.Item>
                          <Form.Item name="description" label="" rules={[{ required: true,message:"" }]}>
                            <TextArea
                              placeholder=""
                              autoSize={{ minRows: 3, maxRows: 5 }}
                            />
                          </Form.Item>
                          <Form.Item name="requirement" label="" rules={[{ required: true,message:"" }]}>
                            <TextArea
                              placeholder=""
                              autoSize={{ minRows: 3, maxRows: 5 }}
                            />
                          </Form.Item>
                        </Form>
                      </Spin>
                      
                    </Modal>
                  </Footer>
                </Layout>
                
            </DocumentTitle>
        )
    }
}
